body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 20px;
  background: #FFF;
}
input {
  margin-right: 10px;
  position: relative;
  bottom: 3px;
}
a {
  color: red;
  text-decoration: none;
}
button,
input {
  border: none;
  border: 1px solid #cccccc;
  color: #474747;
}
.master-wrapper {
  width: 40%;
  margin: 80px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Shadows Into Light', cursive;
}
.header {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__image {
    width: 580px;
  }
}
.list-section {
  background: #EEEEEE;
  padding: 20px;
  width: 400px;
  overflow: auto;
  box-shadow: 2px 4px #999999;

  &__heading-container {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  &__heading {
    font-size: 30px;
    margin-top: 10px;
    text-decoration: underline;
  }
  &__item--checked {
    text-decoration: line-through;
    color: #888888;
  }
  &__formField {
    margin-top: 10px;
  }
  &__addField {
    padding: 5px;
    font-size: 15px;
  }
  &__addButton {
    padding: 5px;
    font-size: 15px;
    width: 32px;
  }
}
.search-section {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}
.searchBar {
  padding: 5px;
  font-size: 15px;
}
.clearButton {
  padding: 5px;
  font-size: 15px;
  width: 32px;
}
.searchicon {
  width: 25px;
  height: 25px;
  margin-right: 10px;
}

@media screen and (max-width: 600px) {
  .master-wrapper {
    width: 80%;
    margin-top: 40px;
  }
  .header__image {
    width: 100%;
  }
  .list-section {
    width: 90%;
  }
}
